/** @format */

import React, { useState, useEffect, Fragment } from "react";

import { connect } from "react-redux";
import { Button, Paper, Grid } from "@mui/material";

import { KeyboardArrowDown } from "@mui/icons-material";

import { useToggle } from "../../context/useToggle";
import AdminSidebar from "./AdminSidebar";
import LawyerSidebar from "./LawyerSidebar";
import FirmSidebar from "./FirmSidebar";
import CustomerSidebar from "./CutomerSidebar";
import "./style.css";

const Sidebar = ({ user }) => {
  const [controlToggle, setControlToggle] = useToggle(true);

  const [localuser, setLocalUser] = useState({
    username: "Admin",
    is_firm: false,
    is_lawyer: true,
    is_client: false,
  });

  useEffect(() => {
    if (user) {
      setLocalUser({
        username: user.username,
        is_firm: user.is_firm,
        is_lawyer: user.is_lawyer,
        is_client: user.is_client,
      });
    }
  }, [user]);

  const SidebarRedirect = () => {
    if (localuser.is_firm) {
      return <FirmSidebar />;
    } else if (localuser.is_client) {
      return <CustomerSidebar />;
    } else if (localuser.is_lawyer) {
      return <LawyerSidebar />;
    } else {
      return <AdminSidebar />;
    }
  };

  // const [state, dispatch] = useContext(UserContext);
  return (
    <Fragment>
      <Paper elevation={3}>
        <Button
          variant="text"
          fullWidth
          endIcon={<KeyboardArrowDown />}
          sx={{
            padding: "0.5em",
            marginTop: "3.5em",
            marginBottom: "1rem",
            textTransform: "none",
          }}
          onClick={() => setControlToggle(controlToggle)}
        >
          Controls
        </Button>
        {controlToggle ? <AdminSidebar /> : null}
      </Paper>
    </Fragment>
  );
};
const mapStateToProps = (state) => ({
  // isAuthenticated: state.auth.isAuthenticated
  user: state.auth.user,
});

export default connect(mapStateToProps)(Sidebar);

